  top: 0;
  z-index: 99;
}

.header .col {
  gap: 100px;
  align-items: center;
  justify-content: space-between;
}

.header__catalog {
  display: flex;
  gap: 14px;
  align-items: center;
  justify-content: flex-start;
  flex: 1 0 auto;
