}

.header__catalog .search input {
  width: 100%;
  max-width: 365px;
  height: 52px;
  padding: 10px 32px 10px 60px;
  background: #FFFFFF url("../svg/search.svg") 24px center no-repeat;
  border: 1px solid #CED8EB;
