.dialog {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  overscroll-behavior: contain;
  display: none;
}

.dialog.show {
  display: inline-block;
}

.dialog-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(7, 14, 24, 0.2);
}

.dialog-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-top: 104px;
  pointer-events: none;
  overscroll-behavior: contain;
  padding-bottom: 40px;
}

.dialog__catalog {
  pointer-events: fill;
  background: #F4F6F9;
  padding: 4px;
  border-radius: 16px;
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  gap: 28px;
}

.dialog__catalog-nav {
  padding: 24px 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 60px;
}

.dialog__catalog-title {
  margin: 0px 0 12px;
  padding: 0 24px;
  font-weight: 600;
  font-size: 32px;
  line-height: 140%;
  color: #070E18;
}

.dialog__catalog-item {
  margin: 0 0 8px;
  width: 100%;
  min-width: 290px;
}

.dialog__catalog-link {
  width: 100%;
  border: none;
  background: none;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 8px;
  border-radius: 12px;
  gap: 20px;
}

.dialog__catalog-link.active {
  background: #fff;
}

.dialog__catalog-link .icon {
  width: 52px;
  height: 52px;
  border-radius: 12px;
  background: #cce8ff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 52px;
}

.dialog__catalog-link .text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 4px;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #4D5F7D;
}

.dialog__catalog-link .text strong {
  font-weight: 600;
  font-size: 18px;
  line-height: 125%;
  color: #070E18;
}

.dialog__catalog-content {
  padding: 24px;
  border-radius: 16px;
  background: #fff;
  flex: 1 0 auto;
  overscroll-behavior: contain;
  overflow: auto;
  max-height: calc(100vh - 144px);
}

.dialog__catalog-pane {
  display: none;
  padding-bottom: 60px;
  width: 100%;
  gap: 40px;
}

.dialog__catalog-pane .sticky {
  position: sticky;
  top: 0;
}

.dialog__catalog-pane > div {
  flex: 0 1 50%;
}

.dialog__catalog-pane.active {
  display: flex;
  align-items: flex-start;
}

.dialog__catalog-pane .title {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 8px;
  margin: 0 0 24px;
  font-weight: 600;
  font-size: 24px;
  line-height: 125%;
  color: #070E18;
}

.dialog__catalog-pane .title span {
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #7992B2;
}

.dialog__contacts {
  padding: 24px;
  width: 430px;
  background: #fff;
  border-radius: 16px;
  margin: 0 0 12px;
  box-shadow: 0px 20px 30px rgba(25, 55, 143, 0.1);
  pointer-events: fill;
}

.dialog__contacts .title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 0 16px;
  font-weight: 600;
  font-size: 20px;
  line-height: 140%;
  color: #070E18;
}

.dialog__contacts .title a {
  font-size: 42px;
  font-weight: 200;
  color: #4d5f7d;
  width: 36px;
  height: 36px;
  text-align: center;
  line-height: 32px;
}

.dialog__contacts-item {
  margin: 0 0 12px;
  padding: 8px 16px;
  background: #FFFFFF;
  border: 1px solid #F0F4FA;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 4px;
}

.dialog__contacts-item:last-of-type {
  margin: 0;
}

.dialog__contacts-item span {
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #7992B2;
}

.dialog__contacts-item a {
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  color: #0080FF;
}

.dialog__contacts form p {
  margin: 0 0 16px;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #4D5F7D;
}

.dialog__contacts form input {
  margin: 0 0 16px;
  width: 100%;
  max-width: 365px;
  height: 52px;
  padding: 10px 32px;
  background: #FFFFFF;
  border: 1px solid #f0f4fa;
  border-radius: 8px;
}

.dialog__contacts form .btn {
  width: 100%;
}

.dialog__contacts form span {
  display: inline-block;
  margin: 16px 0 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 140%;
  color: #A5AFB8;
}

.spoiler-pane {
  display: none;
}

.spoiler-pane.active {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.spoiler-pane .link {
  padding: 8px 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #4D5F7D;
}

.spoiler-pane .link:hover {
  color: #0080ff;
}

.spoiler-grid {
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 12px;
}

.spoiler-link {
  font-weight: 600;
  font-size: 18px;
  line-height: 125%;
  color: #070E18;
  padding: 8px 16px;
  background: #F4F6F9;
  border-radius: 8px;
  width: fit-content;
  white-space: nowrap;
  cursor: pointer;
}

.spoiler-link.active {
  color: #0080FF;
  background: #E3EAF7;
}

